#page-numbers {
  list-style: none;
  display: -ms-flexbox;
  display: flex;
}

#page-numbers > li {
  margin-right: 1em;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: pointer;
}

#page-numbers li.active {
  background-color: #4CAF50;
  color: white;
}

#page-numbers li:hover:not(.active) {background-color: #ddd;}


